import cache from 'utils/cache'
import request from 'core/http'
import log from 'utils/log'
import utils from 'utils/common'

export function getToken () {
  return cache.get('token')
}

/**
 * 
 * @param option
 * @param next
 *  router.beforeEach((to, from, next) = > {
        LoginStatus.check({
            type: 'wechatOauth',
            redirectUrl: '/api/wechat/auth'
        }, next);
    })
 * @returns {Promise<any>}
 */
export function check (option = {}, next) {
  const _check = check.bind(null, option, next)
  // token
  if (!getToken()) {
    updateToken().then(_check)
    return
  }

  const data = {
    type: option.type || 'wechatOauth',  // wechatOauth:; login:
    isRedirect: option.isRedirect || 1,    // 1 or 0
    redirectUrl: option.redirectUrl || '/login',
    url: option.url || location.pathname,  // 
    callbackUrl: option.callbackUrl || location.pathname + location.search // 
  }

  // 
  if (utils.url.params('usertest')) return next()

  const loginStatus = cache.get('isLogin')
  if (!loginStatus) {
    if (data.isRedirect === 1 && data.type === 'login') {
      next({
        path: data.redirectUrl,
        query: { callback: data.callbackUrl }
      })
    } else if (data.type === 'wechatOauth') {
      if (cache.get('isAuth')) return next()
      const callback = location.origin + data.callbackUrl
      let url = 'window.config.api.url' + data.redirectUrl +
        '?callback=' + (encodeURIComponent(callback)) +
        '&token=' + getToken() +
        '&type=' + 'mp'
      if (url.params('usertest')) {
        url += '&usertest=' + url.params('usertest')
      }
      cache.set('isLogin', 2)
      window.location.href = url
    }
  } else if (loginStatus === 2) {
    isLogin('/api/judge/logins', option, next)
  } else {
    typeof next === 'function' && next()
  }
}

export function updateToken () {
  return request.get('/api/init').then(result => {
    if (result.result) {
      cache.set('token', result.token)
    } else {
      throw new Error('token')
    }
  })
}

/*  */
function isLogin (url, option, next) {
  log.debug('')
  request(url).then(res => {
    if (res.data.is_auth === 1 && res.data.is_user === 0) { // 
      cache.set('isLogin', 0)
      cache.set('isAuth', 1)

      check({
        type: 'login',
        redirectUrl: '/binding',
        url: option.url,
        callbackUrl: option.callbackUrl
      }, next)
    } else if (res.data.is_auth === 0) {  // 
      cache.set('isAuth', 0)
      cache.set('isLogin', 0)

      // check
      check(option, next)
    } else if (res.data.is_auth === 1 && res.data.is_user === 1) { // 
      cache.set('isAuth', 1)
      cache.set('isLogin', 1)

      check({
        type: 'login',
        redirectUrl: '/login',
        url: option.url,
        callbackUrl: option.callbackUrl
      }, next)
    }
  }, err => {
    log.error('!')
    // app.alert.error('!')
    cache.set('isLogin', 0)
    cache.set('isAuth', 0)
    // check
    check(option, next)
  })
}

export default {
  getToken,
  updateToken,
  check
}



